import $ from 'jquery';
require("sumoselect/jquery.sumoselect.min");

$(document).ready(function () {
    $('.jsSelect').each(function () {

        const that = $(this);
        const searchTitle = that.data('search')

        $(this).SumoSelect({
            search: true,
            clearAll: true ,
            forceCustomRendering: true,
            captionFormat: '{0} Выбрано',
            captionFormatAllSelected: '{0} Выбрано',
	        noMatch: 'Ничего не найдено'
        });


        setTimeout(function () {
            that.siblings('.jsSelect').find('.search-txt').attr("placeholder", searchTitle);
            that.siblings('.optWrapper ').find('.btnOk').text('Применить');
            that.siblings('.optWrapper ').find('.reset-all').text('Сбросить');

        },10)
    })

    $('body').on('click', '.opt' , function () {
        $(this).closest('.SumoSelect').addClass('is-changed')
    })
});
