import Swiper, { Navigation, Pagination, Autoplay, Grid, EffectFade, Thumbs  } from 'swiper';

const jsSliderIntro = new Swiper('.jsSliderIntro .slider__inner', {
	modules: [Navigation, Pagination, Autoplay, EffectFade],
    pagination: {
        el: ".jsSliderIntro .swiper-pagination",
        clickable: true,
        bulletClass: "slider__pagination-item",
    },
    navigation: {
        nextEl: '.jsSliderIntro .swiper-button-next',
        prevEl: '.jsSliderIntro .swiper-button-prev',
    },
    autoplay: {
		delay: 5600,
        disableOnInteraction: false, 
	},
    waitForTransition: false,
    effect: "fade",
	loop: false,
	slidesPerView: 1,
	speed: 400,

	breakpoints: {
		0:{
            pagination: {
                el: ".jsSliderIntro .swiper-pagination",
                clickable: true,
                bulletClass: "slider__pagination-item",
            },
		},
		576: {
            pagination: {
                el: ".jsSliderIntro .swiper-pagination",
                clickable: true,
                bulletClass: "slider__pagination-item",
            },
		},

		1440: {
            pagination: {
                el: ".jsSliderIntro .swiper-fraction",
                type: 'fraction',
                formatFractionCurrent: function(number) {
                    if (number < 10) {
                    number = '0' + number;
                    }
                return number;
                },
                formatFractionTotal: function(number) {
                    if (number < 10) {
                    number = '0' + number;
                    }
                return number;
                },
                renderFraction: function (currentClass, totalClass) {
                    return '<span class="slider__fraction-item slider__fraction-item--current jsFractionCurrent ' + currentClass + '"></span>' +
                            '<span class="slider__fraction-line"></span>' +
                            '<div class="slider__fraction-item slider__fraction-item--next jsFractionNext"></div>' +
                            '<span class="slider__fraction-item slider__fraction-item--total jsFractionTotal ' + totalClass + '"></span>';
                }
            },
		},
	}
});

paginationChange(jsSliderIntro, 'jsSliderIntro');
jsSliderIntro.update();

function paginationChange(e, w){
    e.on('slideChange update', function () {
        let thisSlider = $('.' + w + '');
        let next = thisSlider.find('.jsFractionNext');
        let current = parseInt(thisSlider.find('.jsFractionCurrent').text(), 10);
        let total = parseInt(thisSlider.find('.jsFractionTotal').text(), 10);
        let line = thisSlider.find('.slider__fraction-line');

        line.removeClass('is-animate');
      
        setTimeout(function() {
            line.addClass('is-animate');
        }, 1);

        if (current < total){
            next.text("0" + (current + 1))
        } else {
            next.text("0" + 1)
        }
     
    });

};
