import noUiSlider from 'nouislider';

let rangeSliders = document.querySelectorAll('.jsRange');

[].slice.call(rangeSliders).forEach(function (slider, index) {
    
    let rangeSlider = slider.querySelector('.jsRangeSlider');
    let inputMin = slider.querySelector('.jsRangeInputMin');
    let inputMax = slider.querySelector('.jsRangeInputMax');
    let valueMin = Number(inputMin.getAttribute('min'));
    let valueMax = Number(inputMax.getAttribute('max'));
    let countMin = slider.querySelector('.jsRangeCountMin');
    let countMax = slider.querySelector('.jsRangeCountMax');
    
    let inputs = [inputMin, inputMax];

    noUiSlider.create(rangeSlider, {
        start: [valueMin, valueMax],
        step: 1,
        connect: true,
        range: {
            'min': valueMin,
            'max': valueMax
        },
    });

    inputMin.addEventListener('input', function() {
        this.value = this.value.replace(/[^\d.]/g, '');
    })
    inputMax.addEventListener('input', function() {
        this.value = this.value.replace(/[^\d.]/g, '');
    })

    inputMin.addEventListener('change', function () {
        rangeSlider.noUiSlider.set([Number(this.value), null]);
    });

    inputMax.addEventListener('change', function () {
        
        rangeSlider.noUiSlider.set([null, Number(this.value)]);
    });

    rangeSlider.noUiSlider.on('update', function (values, handle) {
        inputs[handle].value = Math.round(values[handle]);

        check(inputMin,countMin);
        check(inputMax,countMax);
    });
    
    function check(input,count) {
        count.innerHTML = Math.round(input.value)
    }
});

let rangePriceSliders = document.querySelectorAll('.jsRangePrice');

[].slice.call(rangePriceSliders).forEach(function (slider, index) {
    
    let rangeSlider = slider.querySelector('.jsRangeSlider');
    let inputMin = slider.querySelector('.jsRangeInputMin');
    let inputMax = slider.querySelector('.jsRangeInputMax');
    let valueMin = Number(inputMin.getAttribute('min'));
    let valueMax = Number(inputMax.getAttribute('max'));

    let countMin = slider.querySelector('.jsRangeCountMin');
    let countMax = slider.querySelector('.jsRangeCountMax');
    let digitMin = slider.querySelector('.jsRangeDigitMin');
    let digitMax = slider.querySelector('.jsRangeDigitMax');
    let digit = inputMin.getAttribute('data-digit');
    
    let inputs = [inputMin, inputMax];

    noUiSlider.create(rangeSlider, {
        start: [valueMin, valueMax],
        step: 1,
        connect: true,
        range: {
            'min': valueMin,
            '40%': valueMax / 100,
            'max': valueMax
        },
    });

    inputMin.addEventListener('input', function() {
        this.value = this.value.replace(/[^\d.]/g, '');
    })
    inputMax.addEventListener('input', function() {
        this.value = this.value.replace(/[^\d.]/g, '');
    })

    inputMin.addEventListener('change', function () {
        rangeSlider.noUiSlider.set([Number(this.value), null]);
    });

    inputMax.addEventListener('change', function () {
        
        rangeSlider.noUiSlider.set([null, Number(this.value)]);
    });

    rangeSlider.noUiSlider.on('update', function (values, handle) {
        inputs[handle].value = Math.round(values[handle]);

        check(inputMin,countMin,digit, digitMin);
        check(inputMax,countMax,digit, digitMax);
    });
    
    function check(input,count,digit,type) {
        if((input.value / digit) >= 1000){
            count.innerHTML = Math.round(input.value / (digit * 1000))
            let types = type.getAttribute('data-type').split(",", 2);
            type.innerHTML = types[1];
        } else{
            count.innerHTML = Math.round(input.value / digit)
            let types = type.getAttribute('data-type').split(",", 2);
            type.innerHTML = types[0];
        }
    }
});